	module.exports = {"data":{"markdownRemark":{"html":"<h3>Day 1</h3>\n<div class=\"row\">\n<div class=\"column\">\n<h4>JS Objects, For Loops, Array Manipulation</h4>\n<ul>\n<li>includes()</li>\n<li>Push, Pop, .length, sort()</li>\n<li>Array Practice</li>\n<li>Spread operator</li>\n<li><em>this</em> keyword</li>\n<li>Dot notation</li>\n<li>Scoping, Global Scope, Hoisting</li>\n<li>Methods vs Properties</li>\n</ul>\n</div>\n</div>\n<hr>\n<h3>Day 2</h3>\n<div class=\"row\">\n<div class=\"column\">\n<h4>In-Depth ES6 JS</h4>\n<ul>\n<li>Arrow functions</li>\n<li>Switch statement</li>\n<li>try/catch/finally statement</li>\n<li>Ternary Operator</li>\n</ul>\n</div>\n</div>\n<hr>\n<h3>Day 3</h3>\n<div class=\"row\">\n<div class=\"column\">\n<h3>Classes in Object-Oriented Programming (OOP)</h3>\n<ul>\n<li>( baking pie metaphor )</li>\n<li>Constructor</li>\n<li>Methods</li>\n<li>Instantiation (Inheritance &#x26; Supers)</li>\n</ul>\n</div>\n</div>\n<hr>\n<h3>Day 4</h3>\n<div class=\"row\">\n<div class=\"column\">\n<h3>Git &#x26; The Terminal</h3>\n<ul>\n<li>Command Line Interface (CLI)</li>\n<li>Commands parameters / Paths</li>\n<li>Showing files and changing directories</li>\n<li>Source Control Git</li>\n<li>Commits, Repos, Github, Branches</li>\n<li>Local v Remote Repos</li>\n</ul>\n</div>\n</div>\n<hr>\n<h3>Day 5</h3>\n<div class=\"row\">\n<div class=\"column\">\n<h4>Bootstrap 4</h4>\n<ul>\n<li>Problems Bootstrap Solves</li>\n<li>Advantages with Bootstrap</li>\n<li>Animation requires jQuery</li>\n<li>Grid Layout</li>\n<li>Navigation Components</li>\n<li>Wrapping Rows Dynamicly xs sm md lg</li>\n</ul>\n</div>\n</div>\n<hr>\n<h3>Weekly Project</h3>\n<div class=\"row\">\n<div class=\"column\">\n<h4>Student Portfolio</h4>\n<ul>\n<li>Layout your page using Flexbox or Bootstrap.</li>\n<li>A header with your name and your job title (ie. Web Developer, Full Stack Developer, etc..)</li>\n<li>A footer that sticks to the bottom of the screen, commonly referred to as a sticky footer.</li>\n<li>The footer should contain contact information with an email icon and an email address listed next to it.</li>\n<li>An image of yourself so everyone knows who you are.</li>\n<li>Clickable GitHub icon linking to your GitHub.</li>\n<li>Clickable LinkedIn icon linking to your LinkedIn.</li>\n<li>Other Professional Social Media (ie. Twitter, Facebook, Instagram, etc..) icons</li>\n<li>Using jQuery, when your icons are hovered over they should transform in size using CSS to show animation.</li>\n<li>List your tech-related skills.</li>\n<li>Dynamically list your GitHub repositories.</li>\n<li>The repositories should be hyperlinks to the actual repository page on your GitHub account.</li>\n<li>Your list of repositories should be styled.</li>\n<li>Style your page using CSS.</li>\n</ul>\n</div>\n</div>","headings":[{"depth":3,"value":"Day 1"},{"depth":4,"value":"JS Objects, For Loops, Array Manipulation"},{"depth":3,"value":"Day 2"},{"depth":4,"value":"In-Depth ES6 JS"},{"depth":3,"value":"Day 3"},{"depth":3,"value":"Classes in Object-Oriented Programming (OOP)"},{"depth":3,"value":"Day 4"},{"depth":3,"value":"Git & The Terminal"},{"depth":3,"value":"Day 5"},{"depth":4,"value":"Bootstrap 4"},{"depth":3,"value":"Weekly Project"},{"depth":4,"value":"Student Portfolio"}],"frontmatter":{"title":"Week 2 - Front End Foundations Syllabus"}}},"pathContext":{"slug":"/courses/frontend-foundations/syllabus/"}}

